:root {
    --width: 85%;
    --color-primary: #B3DEC1;
}

*,
*::before,
*::after {
    margin: 0;
    box-sizing: border-box;
}

@import url('https://fonts.googleapis.com/css2?family=Kaushan+Script&family=Roboto:wght@400;700&display=swap');

.App {
    min-height: 100vh;
    font-family: 'Roboto', sans-serif;
    background: url("https://unsplash.com/photos/PhYq704ffdA");
    display: grid;
    background-attachment: fixed;
    overflow: hidden;
}

.hero {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-basis: 40%;
}

.hero__img {
    border-radius: 50%;
    margin: 2rem;
}

// .hero__title {
//     color: white;
// }

.hero__greetings {
    max-width: 720px;
    width: var(--width);
    padding: 1rem 2.5rem;
    background-color: var(--color-primary);
    border-radius: 0.75rem;
    margin: 3rem;
}

.buttons__div {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;
}

.button__link {
    max-width: 720px;
    width: var(--width);
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 3rem;
    border-radius: 0.75rem;
    background: linear-gradient(to right, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.1));
    backdrop-filter: blur(0.5rem);
    padding: 1rem 1.5rem;
    text-align: center;
    cursor: pointer;
}

.button__img {
    width: 3rem;
    height: auto;
    border-radius: 0.5rem;
}

.button__p {
    // color: white;
    & span {
        font-weight: 700;
        text-transform: uppercase;
    }
}

.blackscreen {
    position: fixed;
    inset: 0;
    backdrop-filter: blur(5px);
    display: grid;
    place-items: center;
    transition: background-color 100ms ease-in-out;
    z-index: 10;

}

.active {
    background-color: rgba($color: #000000, $alpha: 0.5);
}

.detail__card {
    padding: 1rem 6rem;
    max-width: 720px;
    width: var(--width);
    background-color: white;
    border-radius: 0.75rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    transform: scale(0);
    position: relative;

    transition: transform 100ms ease-in-out;

    &.grow {
        transform: scale(1)
    }
}

.detail__title {
    margin-bottom: 1.5em;
}

.detail__paragraph {
    width: 100%;
    margin-bottom: 1rem;
    text-align: left;
}

.detail__button {
    text-decoration: none;
    text-transform: uppercase;
    padding: 0.25em 0.75em;
    border-radius: 0.5rem;
    background-color: var(--color-primary);
    color: white;
    margin-top: 1rem;
}

.fa-xmark {
    position: absolute;
    top: 1.5rem;
    right: 2rem;
    font-size: 1.5rem;
    cursor: pointer;
    // color: #eee;
}

footer {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    margin-top: 2rem;
    padding: 1rem;
}

.logo__div {
    display: flex;
    align-items: center;
    gap: 1.5rem;
}

.logo {
    font-family: 'Kaushan Script', cursive;
    color: #fff;
}

.attribution {
    color: #eee;

    & a {
        color: #eee;
        text-decoration: underline;
    }
}

